import React, { useEffect } from 'react';
import '@chatui/core/es/styles/index.less';
import Chat, { Bubble, useMessages } from '@chatui/core';
import '@chatui/core/dist/index.css';
import { BASE_URL } from './config.js';
import axios from 'axios';

const initialMessages = [
  // {
  //   type: 'text',
  //   content: { text: '~' },
  //   user: { avatar: '//gw.alicdn.com/tfs/TB1DYHLwMHqK1RjSZFEXXcGMXXa-56-62.svg' },
  // },
];
var text1;
var lan=localStorage.getItem("lang");
		switch (lan) {
		case "zhTW":
      	text1=""
		    break;
		  case "thai":
		  text1=""
			break;
		  case "enUS":
		  text1="Customer Service"
		 
		    break;
		 default:
		 text1=""
		
}
const navbar= 
      {
		  title:text1,
          leftContent: {
			icon:'chevron-left'
		},
       
      }

const toolbar= [
      {
        // type: 'image',
        // icon: 'image',
        // title: '',
      }
]
// 
const defaultQuickReplies = [
  {
    icon: 'message',
    name: '',
    isNew: true,
    isHighlight: true,
  }
];
var socket;
var tokens;
export default function() {
  // 
  const { messages, appendMsg, setTyping } = useMessages(initialMessages);
  
      useEffect(() => {
      	var token=localStorage.getItem("token");
		axios({
		      method: 'post',
			  params:{
			  	'token':token
			  },
		      url: BASE_URL+'/api/user/createimtoken'
		    }).then(response => {
			 
		      console.log(JSON.stringify(response.data))
		      if(response.status=='200'){
		      				if(response.data.login.state=='2'){
		      					
		      				}else{
		      					tokens=response.data.data.im_token;
								socket = new WebSocket("ws://192.168.2.128:20000?token="+encodeURIComponent(tokens));
								
								socket.onopen = function(event) { console.log("WebSocket "); }; 
								//  
								socket.onmessage = function(event) { 
									console.log("" + event.data); 
									var res=JSON.parse(event.data)
									appendMsg({
									  type: 'text',
									  user: { avatar: '//gw.alicdn.com/tfs/TB1DYHLwMHqK1RjSZFEXXcGMXXa-56-62.svg' },
									  content: { text: res.content },
									});
								}; 
		      				}			
		    }
		});
}, [])
      	    
        
      
  // 
  function handleSend(type, val) {
	  var s={'token':tokens,
	'type':1,
	'content':val};
	socket.send(JSON.stringify(s));
    if (type === 'text' && val.trim()) {
      // TODO: 
      appendMsg({
        type: 'text',
        content: { text: val },
        position: 'right',
      });

      

      
    }
  }
  

  //  item 
  function handleQuickReplyClick(item) {
    handleSend('text', item.name);
  }
  function onNavbarClick(item) {
    handleSend('text', item.name);
  }

  function renderMessageContent(msg) {
    const { type, content } = msg;

    // 
    switch (type) {
      case 'text':
        return <Bubble content={content.text} />;
      case 'image':
        return (
          <Bubble type="image">
            <img src={content.picUrl} alt="" />
          </Bubble>
        );
      default:
        return null;
    }
  }
 function onToolbarClick(item, ctx) {
      // 
	 
      if (item.type === 'image') {
		  const input = document.createElement('input')
		      //
		    input.type = 'file'
		      //
		    input.accept = 'image/*'
		    input.click()
		    input.onchange = (ctx) => {
		      console.log('inputfiles',input.files);
		    
		      if (input.files) {
		        const file = input.files[0]
				appendMsg({
				  type: 'image',
				  content: { picUrl: URL.createObjectURL(file) },
				  position: 'right',
				});
		      }
		    }
			
		 
        // ctx.util.chooseImage({
        //   // multiple: true, // 
        //   success(e) {
        //     if (e.files) { //  h5 
        //       const file = e.files[0];
        //       // 
        //       ctx.appendMessage({
        //         type: 'image',
        //         content: {
        //           picUrl: URL.createObjectURL(file)
        //         },
        //         position: 'right'
        //       });

              

        //     } else if (e.images) { //  app 
        //       // ..
        //     }
        //   },
        // });
      }
    }
	setTimeout(() => {
		
      socket.onclose = function(event) { console.log("WebSocket "); }
	  var btn = document.getElementsByClassName("Navbar-left")[0];
		btn.onclick = function(){
		    window.location.href="/my";
		};
	}, 1000);
	function onInputFocus(){
		// var btn = document.getElementsByClassName("MessageList")[0];
		// var scrollHeight=btn.scrollHeight;
		// var height=btn.style.height;
		// console.log("scrollHeight="+scrollHeight+",height="+height)
		
		// btn.scrollTop = btn.scrollHeight-40;
	}
  return (<>
    <Chat
	onInputFocus={onInputFocus}
      navbar={navbar}
	  onNavbarClick={onNavbarClick}
      messages={messages}
      renderMessageContent={renderMessageContent}
      quickReplies={defaultQuickReplies}
      onQuickReplyClick={handleQuickReplyClick}
      onSend={handleSend}
	  toolbar={toolbar}
	  onToolbarClick={onToolbarClick}
    />
	
	</>
  );
}